// Variables
@import 'colors';
@import 'variables';

// Styles
@import 'utilities';
@import 'buttons';
@import 'links';

html {
  line-height: 1.5;
  font-size: 18px;
  color: $color2;
}

body {
  background-color: $color1;
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Roboto Slab', serif;
  color: $color5;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: $color5;

  &:hover {
    text-decoration: underline;
  }
}

hr {
  opacity: 0.25;
}

.avatar {
  border: 2px dashed $color3;
  border-radius: 100%;
  display: block;
  margin: 0 auto 25px;

  @media (max-width: $small-screen-max-width) {
    margin-bottom: 0;
  }
}

.post-divider {
  margin-top: 2em;
}

.highlighter-rouge {
  padding: .2rem .4rem;
  font-size: 90%;
  color: #bd4147;
  border-radius: .25rem;
}

.flexbox-container {
  display: -ms-flex;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
}

.sidebar {
  background-color: $color2;
  color: $color4;
  flex: 1;
  padding: 1.2em;
  min-width: $sidebar-width;
}

.content {
  flex: 2 1 auto;
  padding: 0 3em 3em;
  max-width: $content-width;
}

@media (min-width: $large-screen-min-width) {
  .flexbox-container {
    -webkit-flex-direction: row;
    flex-direction: row;
  }

  .sidebar {
    max-width: $sidebar-width;
  }
}
